import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Card, CardContent, CardDescription, CardFooter, CardHeader, CardTitle } from "@/components/ui/card";
import { ArrowRight, ArrowLeft } from "lucide-react";
import { RadioGroup, RadioGroupItem } from "@radix-ui/react-radio-group";
import { Checkbox } from "@radix-ui/react-checkbox";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@radix-ui/react-select";

export function OnboardingSteps() {
  const [step, setStep] = useState(1);
  const totalSteps = 3;
  const [role, setRole] = useState<"student" | "organizer" | "admin" | "">("");
  const [phone, setPhone] = useState("");
  const [interests, setInterests] = useState<string[]>([]);

  const allInterests = [
    "Technology",
    "Design",
    "Business",
    "Artificial Intelligence",
    "Marketing",
    "Finance",
    "Health",
    "Sustainability",
  ];

  const toggleInterest = (interest: string) => {
    setInterests((prev) =>
      prev.includes(interest)
        ? prev.filter((i) => i !== interest)
        : [...prev, interest]
    );
  };

  const nextStep = () => {
    if (step === 3 && interests.length < 3) {
      alert("Please select at least 3 interests.");
      return;
    }

    if (step < totalSteps) {
      setStep(step + 1);
    } else {
      if (role === "student") {
        window.location.href = "/dashboard/student";
      } else if (role === "organizer") {
        window.location.href = "/dashboard/organizer";
      } else if (role === "admin") {
        window.location.href = "/dashboard/admin";
      } else {
        alert("Please select a role");
      }
    }
  };

  const prevStep = () => {
    if (step > 1) {
      setStep(step - 1);
    }
  };

  return (
    <Card className="w-full max-w-md">
      <CardHeader className="space-y-1">
        <div className="flex items-center justify-between">
          <CardTitle className="text-2xl font-bold">
            {step === 1 && "Create an account"}
            {step === 2 && "Choose your role"}
            {step === 3 && "Select your interests"}
          </CardTitle>
          <div className="text-sm text-muted-foreground">
            Step {step} of {totalSteps}
          </div>
        </div>
        <CardDescription>
          {step === 1 && "Enter your information to create your StudentHub account"}
          {step === 2 && "Select your role to personalize your experience"}
          {step === 3 && "Choose your interests to get personalized recommendations"}
        </CardDescription>
      </CardHeader>

      <CardContent className="space-y-4">
        {step === 1 && (
          <>
            <div className="grid grid-cols-2 gap-4">
              <div className="space-y-2">
                <Label htmlFor="first-name">First name</Label>
                <Input id="first-name" placeholder="John" />
              </div>
              <div className="space-y-2">
                <Label htmlFor="last-name">Last name</Label>
                <Input id="last-name" placeholder="Doe" />
              </div>
            </div>
            <div className="space-y-2">
              <Label htmlFor="email">Email</Label>
              <Input id="email" type="email" placeholder="john.doe@university.edu" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="phone">Phone</Label>
              <Input
                id="phone"
                type="text"
                placeholder="123-456-7890"
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="space-y-2">
              <Label htmlFor="password">Password</Label>
              <Input id="password" type="password" />
            </div>
            <div className="space-y-2">
              <Label htmlFor="confirm-password">Confirm Password</Label>
              <Input id="confirm-password" type="password" />
            </div>
          </>
        )}

        {step === 2 && (
          <div className="space-y-4">
            <div className="space-y-2">
              <Label>I am a</Label>
              <RadioGroup value={role} onValueChange={(val) => setRole(val as typeof role)} className="grid gap-4 pt-2">
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="student" id="student" />
                  <Label htmlFor="student" className="flex flex-col">
                    <span className="font-medium">Student</span>
                    <span className="text-sm text-muted-foreground">
                      Access events, connect with peers, and build your portfolio
                    </span>
                  </Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="admin" id="admin" />
                  <Label htmlFor="admin" className="flex flex-col">
                    <span className="font-medium">University Admin</span>
                    <span className="text-sm text-muted-foreground">
                      Manage university events and approve student accounts
                    </span>
                  </Label>
                </div>
                <div className="flex items-center space-x-2 rounded-md border p-4">
                  <RadioGroupItem value="organizer" id="organizer" />
                  <Label htmlFor="organizer" className="flex flex-col">
                    <span className="font-medium">Event Organizer</span>
                    <span className="text-sm text-muted-foreground">Create and manage events for students</span>
                  </Label>
                </div>
              </RadioGroup>
            </div>
          </div>
        )}

        {step === 3 && (
          <div className="space-y-4">
            {(role === "organizer" || role === "admin") && (
              <>
                <div className="space-y-2">
                  <Label>{role === "organizer" ? "Organization Name" : "University Name"}</Label>
                  <Input placeholder={`Enter your ${role === "organizer" ? "organization" : "university"} name`} />
                </div>
                <div className="space-y-2">
                  <Label>Foundation Year</Label>
                  <Input type="number" placeholder="e.g., 1998" />
                </div>
              </>
            )}

            {role === "student" && (
              <>
                <div className="space-y-2">
                  <Label>Date of Birth</Label>
                  <Input type="date" />
                </div>
                <div className="space-y-2">
                  <Label>University Name</Label>
                  <Input placeholder={`Enter your university name`} />
                </div>
              </>
            )}

            {/* Shared Interests */}
            <div className="space-y-2">
              <Label>Select your interests (choose at least 3)</Label>
              <div className="grid grid-cols-2 gap-2 pt-2">
                {allInterests.map((interest) => (
                  <div key={interest} className="flex items-center space-x-2 rounded-md border p-3">
                    <Checkbox
                      id={interest}
                      checked={interests.includes(interest)}
                      onCheckedChange={() => toggleInterest(interest)}
                    />
                    <Label htmlFor={interest}>{interest}</Label>
                  </div>
                ))}
              </div>
            </div>

            <div className="space-y-2">
              <Label>Career Goals</Label>
              <Select>
                <SelectTrigger>
                  <SelectValue placeholder="Choose a goal" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="internship">Internship</SelectItem>
                  <SelectItem value="job">Full-time Job</SelectItem>
                  <SelectItem value="networking">Networking</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        )}
      </CardContent>

      <CardFooter className="flex justify-between">
        <Button variant="outline" onClick={prevStep} disabled={step === 1}>
          <ArrowLeft className="mr-2 h-4 w-4" /> Back
        </Button>
        <Button onClick={nextStep}>
          {step === totalSteps ? "Finish" : <>Continue <ArrowRight className="ml-2 h-4 w-4" /></>}
        </Button>
      </CardFooter>
    </Card>
  );
}
